.leftPanel {
    background-color: rgb(240, 244, 247);
    height: 100%;
    box-shadow: rgb(221 232 241) 1px 0px 0px 0px;
}

.leftPanel-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    list-style-type: none;
    padding-top: 24px;
}

.leftPanel-list-item {
    width: 32px;
    height: 32px;
    margin-bottom: 24px;
}

.leftPanel-list-item.home {
    margin-bottom: 48px;
}

.leftPanel-list-item-image {
    width: 100%;
    height: 100%;
}